import request from '@/utils/request'

// 获取在线小车列表
export function getOnlineAmr(data) {
  return request.get('/amr/onlineAmr', data)
}

// 获取单个在线小车详情
export function getOnlineAmrById(data) {
  return request.get('/amr/getOnlineAmrById', data)
}

// 获取 区域-地图 列表
export function getAreaMonitor() {
  return request.post('/area/monitor')
}

// 获取小车任务详情
export function getAmrTaskInfo(data) {
  return request.get('/amr/getAmrTaskById', data)
}

// 暂停小车
export function pauseAmr(data) {
  return request.post('/amr/taskCommand/pause/' + data.id)
}

// 恢复小车
export function resumeAmr(data) {
  return request.post('/amr/taskCommand/resume/' + data.id)
}

// 根据小车id和地图name获取地图包
export function getSchedulerMapZip(data) {
  return request.get('map/loadMapFileByAmr', data, "blob");
}

// 获取1.0的在线小车列表
export function getOldOnlineAmr() {
  return request.get('dispatch/vehicle/getActiveVehicles')
}