<template>
  <div class="amrDetails-container" v-loading="loading">
    <div class="left">
      <div class="basic">
        <el-page-header @back="goBack" v-if="backPage">
          <template slot="content">
            {{
              amrInfo.amrPo.name
                ? amrInfo.amrPo.name + " " + amrInfo.amrTypePo.name
                : "---"
            }}
          </template>
        </el-page-header>
        <div class="image" ref="imageParent">
          <img :src="imgUrl" id="imageChild" @error="setDefaultImg" />
        </div>
        <div class="info-items">
          <div class="info-item">
            <div class="info-key">设备ID:</div>
            <div>{{ amrInfo.amrPo.id }}</div>
          </div>
        </div>
      </div>
      <div class="function-container">
        <div class="title">常用功能</div>
        <div class="items">
          <div class="item" @click="toAddDevice" v-hasPermi="'device/amr/add'">
            <i class="el-icon-folder-add"></i>
            <span>添加设备</span>
          </div>
          <div class="item" @click="toDeviceList">
            <i class="el-icon-folder-opened"></i>
            <span>设备列表</span>
          </div>
        </div>
      </div>
    </div>
    <div class="right">
      <el-tabs
        v-model="activeName"
        type="border-card"
        @tab-click="handleTabClick"
      >
        <el-tab-pane
          name="1"
          label="参数配置"
          :style="{ height: innerHeight - 121 + 'px' }"
        >
          <div class="title">基础参数</div>
          <table class="table" border="1" cellspacing="0">
            <tr :class="isEditName ? 'tr-editor' : ''">
              <td class="td-left">设备名称</td>
              <td class="td-center">
                <span v-if="!isEditName">{{ amrInfo.amrPo.name }}</span>
                <el-input
                  v-model.trim="amrInfo.amrPo.name"
                  placeholder="请输入设备名称"
                  clearable
                  :maxlength="30"
                  v-else
                />
              </td>
              <td class="td-right">
                <edit-icon
                  :isEdit.sync="isEditName"
                  :data.sync="amrInfo.amrPo.name"
                  @checkEdit="updateVehicleName"
                  @closeAll="closeAll"
                />
              </td>
            </tr>
            <tr :class="isEditArea ? 'tr-editor' : ''">
              <td class="td-left">所在区域</td>
              <td class="td-center">
                <span v-if="!isEditArea">{{
                  domainList[amrInfo.amrPo.areaId]
                }}</span>
                <el-select
                  v-model.trim="amrInfo.amrPo.areaId"
                  placeholder="请选择工作区域"
                  filterable
                  v-else
                >
                  <el-option
                    v-for="(value, key) in domainList"
                    :key="key"
                    :label="value"
                    :value="Number(key)"
                  ></el-option>
                </el-select>
              </td>
              <td class="td-right">
                <edit-icon
                  :isEdit.sync="isEditArea"
                  :data.sync="amrInfo.amrPo.areaId"
                  @checkEdit="updateVehicleArea"
                  @closeAll="closeAll"
                />
              </td>
            </tr>
            <tr :class="isEditGroup ? 'tr-editor' : ''">
              <td class="td-left">设备分组</td>
              <td class="td-center">
                <span v-if="!isEditGroup">{{
                  groupList[amrInfo.amrPo.groupId] || "-- 未绑定 --"
                }}</span>
                <el-select
                  v-model.trim="amrInfo.amrPo.groupId"
                  placeholder="请选择分组"
                  filterable
                  clearable
                  v-else
                >
                  <el-option
                    v-for="(value, key) in groupList"
                    :key="key"
                    :label="value"
                    :value="Number(key)"
                  ></el-option>
                </el-select>
              </td>
              <td class="td-right">
                <edit-icon
                  :isEdit.sync="isEditGroup"
                  :data.sync="amrInfo.amrPo.groupId"
                  @checkEdit="updateVehicleGroup"
                  @open="getGroupList"
                  @closeAll="closeAll"
                />
              </td>
            </tr>
            <tr>
              <td class="td-left">Mac地址</td>
              <td class="td-center">
                <span>{{ amrInfo.amrPo.macAddress }}</span>
              </td>
              <td class="td-right"></td>
            </tr>
            <tr>
              <td class="td-left">IP地址</td>
              <td class="td-center">
                <span>{{ onlineInfo.ip || "--- 未知 ---" }}</span>
              </td>
              <td class="td-right"></td>
            </tr>
            <tr :class="isEditEnable ? 'tr-editor' : ''">
              <td class="td-left">设备状态</td>
              <td class="td-center">
                <span v-if="!isEditEnable">{{
                  amrInfo.amrPo.enable ? "已启用" : "未启用"
                }}</span>
                <el-switch
                  v-model.trim="amrInfo.amrPo.enable"
                  :active-value="1"
                  :inactive-value="0"
                  v-else
                ></el-switch>
              </td>
              <td class="td-right">
                <edit-icon
                  :isEdit.sync="isEditEnable"
                  :data.sync="amrInfo.amrPo.enable"
                  @checkEdit="updateVehicleEnable"
                  @closeAll="closeAll"
                />
              </td>
            </tr>
          </table>
          <div class="title">配置信息</div>
          <table class="table" border="1" cellspacing="0">
            <tr :class="isEditModel ? 'tr-editor' : ''">
              <td class="td-left">充电桩类型</td>
              <td class="td-center">
                <span v-if="!isEditModel">{{ amrInfo.modelNames }}</span>
                <el-select
                  v-model.trim="amrInfo.modelIds"
                  placeholder="请选择充电桩类型"
                  filterable
                  clearable
                  multiple
                  collapse-tags
                  v-else
                >
                  <el-option
                    v-for="(value, key) in modelList"
                    :key="key"
                    :label="value"
                    :value="Number(key)"
                  ></el-option>
                </el-select>
              </td>
              <td class="td-right">
                <edit-icon
                  :isEdit.sync="isEditModel"
                  :data.sync="amrInfo.modelIds"
                  @checkEdit="updateVehicleModel"
                  @closeAll="closeAll"
                />
              </td>
            </tr>
            <tr :class="isEditCharge ? 'tr-editor' : ''">
              <td class="td-left">充电桩绑定</td>
              <td class="td-center">
                <span v-if="!isEditCharge">{{
                  amrInfo.chargePilePo && mapList[amrInfo.chargePilePo.mapId]
                    ? mapList[amrInfo.chargePilePo.mapId].name +
                      "/" +
                      amrInfo.chargePilePo.name +
                      "/" +
                      (amrInfo.forceBind ? "已强制绑定" : "未强制绑定")
                    : "-- 未绑定 --"
                }}</span>
                <span v-else>
                  <el-select
                    v-model.trim="amrInfo.charge"
                    placeholder="请选择充电桩"
                    filterable
                    clearable
                    size="small"
                  >
                    <el-option
                      v-for="(value, key) in chargePileList"
                      :key="key"
                      :label="mapList[value.mapId].name + '/' + value.name"
                      :value="Number(key)"
                    ></el-option>
                  </el-select>
                  <el-switch
                    v-model.trim="amrInfo.forceBind"
                    class="force-bind"
                  ></el-switch
                ></span>
              </td>
              <td class="td-right">
                <edit-icon
                  :isEdit.sync="isEditCharge"
                  :data.sync="amrInfo.charge"
                  @open="getChargePileList"
                  @checkEdit="updateVehicleCharge"
                  @closeAll="closeAll"
                  @cancel="getChargeForceBind"
                  :directUpdate="true"
                />
              </td>
            </tr>
            <tr :class="isEditRest ? 'tr-editor' : ''">
              <td class="td-left">休息站绑定</td>
              <td class="td-center">
                <span v-if="!isEditRest">{{
                  restInfo.restStationId &&
                  restStationMap[restInfo.restStationId]
                    ? restStationMap[restInfo.restStationId].name
                    : "-- 未绑定 --"
                }}</span>
                <el-select
                  v-model.trim="restInfo.restStationId"
                  placeholder="请选择休息站"
                  filterable
                  clearable
                  v-else
                  size="small"
                >
                  <el-option
                    v-for="(value, key) in restList"
                    :key="key"
                    :label="value.name"
                    :value="Number(key)"
                  ></el-option>
                </el-select>
              </td>
              <td class="td-right">
                <edit-icon
                  :isEdit.sync="isEditRest"
                  :data.sync="restInfo.restStationId"
                  @open="getRestList"
                  @checkEdit="updateVehicleRest"
                  @closeAll="closeAll"
                />
              </td>
            </tr>
            <tr :class="isEditRestIdle ? 'tr-editor' : ''">
              <td class="td-left">休息时间(s)</td>
              <td class="td-center">
                <span v-if="!isEditRestIdle">{{
                  restInfo.id ? restInfo.idle : "-- 未配置 --"
                }}</span>
                <el-tooltip
                  content="清空表示删除休息站配置"
                  placement="top"
                  v-else
                >
                  <el-input-number
                    placeholder="空闲回休息站时间"
                    controls-position="right"
                    v-model.number="restInfo.idle"
                    :min="0"
                  >
                  </el-input-number>
                </el-tooltip>
              </td>
              <td class="td-right">
                <edit-icon
                  :isEdit.sync="isEditRestIdle"
                  :data.sync="restInfo.idle"
                  @checkEdit="updateRestIdle"
                  @closeAll="closeAll"
                />
              </td>
            </tr>
            <tr>
              <td class="td-left">充电档位</td>
              <td class="td-center">
                <span>{{
                  amrInfo.chargeLevel && amrInfo.chargeLevel.level3
                    ? `1档: ${amrInfo.chargeLevel.level1}%, 2档: ${amrInfo.chargeLevel.level2}%, 3档: ${amrInfo.chargeLevel.level3}%`
                    : "-- 未设置 --"
                }}</span>
              </td>
              <td class="td-right">
                <el-button type="text" @click="setChargeLevel"
                  >前往设置</el-button
                >
              </td>
            </tr>
          </table>
          <!-- <div class="title">版本号</div>
          <table class="table" border="1" cellspacing="0">
            <tr>
              <td class="td-left">本体软件版本</td>
              <td class="td-center"></td>
              <td class="td-right"></td>
            </tr>
          </table> -->
        </el-tab-pane>
        <el-tab-pane
          name="2"
          label="点位资源"
          :style="{ height: innerHeight - 121 + 'px' }"
        >
          <el-card class="lockedPoints-main" header="已占用点位">
            <div slot="header" class="card-header">
              <span>已占用点位</span>
              <div>
                <el-button type="primary" @click="getLockedPoints"
                  >手动刷新</el-button
                >
                <el-button type="warning" @click="releaseLockedPoints"
                  >释放点位</el-button
                >
              </div>
            </div>
            <div class="point-list">
              <span
                class="point"
                v-for="(point, i) in lockedPoints"
                :key="i"
                v-show="lockedPoints.length !== 0"
              >
                <el-tag>{{ point }}</el-tag>
              </span>
              <span v-show="lockedPoints.length === 0" class="null-data"
                >-- 无占用点位 --</span
              >
            </div>
          </el-card>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import {
  getVehicleDetail,
  getVehicleTypeList,
  updateVehicle,
  updateVehicleArea,
  toggleVehicleEnable,
  updateVehicleGroup,
  updateVehicleModel,
  updateVehicleCharge,
  getVehicleRest,
  updateVehicleRest,
  addVehicleRest,
  deleteVehicleRest,
  getLockedPoints,
  releaseLockedPoints,
  getChargeForceBind,
} from "@/api/vehicle";
import { getChargeGearList } from "@/api/config/chargeGear";
import { getGroupList } from "@/api/account/group";
import {
  getChargePileModelList,
  getChargePileList,
} from "@/api/config/chargePile";
import { getDomainList } from "@/api/config/domain";
import { getMapList } from "@/api/map";
import { getOnlineAmrById } from "@/api/monitor";
import { getRestStationList } from "@/api/config/restStation";
import EditIcon from "./components/editIcon.vue";
import { Message, MessageBox } from "element-ui";
import { isFAKEPoint } from "@/utils/validate";
export default {
  props: ["innerHeight"],
  components: { EditIcon },
  data() {
    return {
      backPage: "", //返回地址
      vehicleId: 0, //设备id
      loading: false, // 等待
      amrInfo: {
        amrPo: {},
        amrTypePo: {},
        chargePileModelPo: [],
        chargePilePo: "",
        modelIds: [],
        modelNames: "",
        charge: "",
        chargeLevel: {},
      }, //设备详情
      restInfo: { id: "", idle: "", restStationId: "" }, //休息站
      imgUrl: "./static/img/device/no-img.png", //默认设备图标
      activeName: "1", //右侧默认展示部分
      typeOptions: {}, //设备类型列表
      domainList: {}, //区域列表
      mapList: {}, // 地图列表
      groupList: {}, //分组列表
      modelList: {}, // 充电桩类型列表
      chargePileList: {}, // 充电桩列表
      restList: {}, // 休息站列表
      isEditName: false, //是否正在修改名称
      isEditArea: false, //是否正在修改区域
      isEditGroup: false, //是否正在修改分组
      isEditModel: false, //是否正在修改充电桩类型
      isEditCharge: false, //是否正在修改充电桩
      isEditEnable: false, //是否正在修改启用状态
      isEditRest: false, //是否正在修改休息站
      isEditRestIdle: false, //是否正在修改休息站时间
      onlineTimer: 0, //获取在线信息定时器
      onlineTime: 2000, //获取在线信息定时器时长
      onlineInfo: {}, //小车在线信息
      lockedPoints: [], //占用点位列表
      restStationMap: {}, //休息站列表
    };
  },
  mounted() {
    this.vehicleId = Number(this.$route.params.id);
    this.backPage = this.$route.query.backPage;
    // 获取区域列表
    getDomainList().then((res) => {
      if (res.state && res.data.data) {
        res.data.data.forEach((item) => {
          this.$set(this.domainList, item.id, item.name);
        });
      }
    });
    // 获取充电桩类型列表
    getChargePileModelList().then((res) => {
      if (res.state && res.data) {
        res.data.forEach((item) => {
          this.$set(this.modelList, item.id, item.description);
        });
      }
    });
    // 获取地图列表
    getMapList().then((res) => {
      if (res.state && res.data.data) {
        res.data.data.forEach((item) => {
          this.$set(this.mapList, item.id, item);
        });
      }
    });
    // 获取休息站列表，用来展示休息站名称
    getRestStationList().then((res) => {
      if (res.state) {
        let list = res.data.list || res.data || [];
        list.forEach((item) => {
          this.$set(this.restStationMap, item.id, item);
        });
      }
    });
    this.getVehicleRest();
    this.getGroupList();
    this.getVehicleDetail();
    this.handleTabClick();
    this.onlineTimer && clearInterval(this.onlineTimer);
    this.onlineTimer = setInterval(() => {
      if (this.onlineTimer) this.handleTabClick();
    }, this.onlineTime);
    this.onResize();
  },
  methods: {
    // 切换子页面
    handleTabClick() {
      if (this.activeName === "1") this.getOnlineAmrInfo();
      if (this.activeName === "2") this.getLockedPoints();
    },
    // 获取设备详情
    getVehicleDetail() {
      let fun = () => {
        this.loading = true;
        getVehicleDetail({ id: this.vehicleId }).then((res) => {
          if (res.state) {
            let { chargePileModelPo, chargePilePo } = res.data;
            let modelIds = [];
            let modelNames = "";
            let charge = "";
            if (chargePileModelPo && chargePileModelPo.length > 0) {
              chargePileModelPo.forEach((item) => {
                modelIds.push(item.id);
                modelNames += item.description;
              });
            } else {
              modelNames = "-- 未绑定 --";
            }
            if (chargePilePo) charge = chargePilePo.id;
            res.data.modelIds = modelIds;
            res.data.modelNames = modelNames;
            res.data.charge = charge;
            this.amrInfo = res.data;
            getChargeGearList({ amrId: this.vehicleId }).then((res) => {
              if (res.state) {
                this.$set(this.amrInfo, "chargeLevel", res.data.list[0] || {});
              }
            });
            this.getChargeForceBind();
            this.imgUrl = `./static/img/device/${res.data.amrTypePo.name}.png`;
            this.loading = false;
          }
        });
      };
      if (this.typeOptions[1]) {
        fun();
      } else {
        // 获取设备类型列表
        getVehicleTypeList().then((res) => {
          if (res.state && res.data) {
            res.data.forEach((item) => {
              this.$set(this.typeOptions, item.id, item.name);
            });
          }
          fun();
        });
      }
    },
    //获取休息站详情
    getVehicleRest() {
      getVehicleRest({ amrId: this.vehicleId }).then((res) => {
        if (res.state && res.data) {
          this.restInfo = {
            id: res.data.id,
            restStationId: res.data.restStationId,
            idle: res.data.idle,
          };
        } else {
          this.restInfo = { id: "", idle: "", restStationId: "" };
        }
      });
    },
    onResize() {
      this.$nextTick(() => {
        document.getElementById("imageChild").style.maxWidth =
          this.$refs.imageParent.offsetWidth - 80 + "px";
        document.getElementById("imageChild").style.maxHeight =
          this.$refs.imageParent.offsetHeight - 50 + "px";
      });
    },
    // 获取小车在线详情
    getOnlineAmrInfo() {
      getOnlineAmrById({ id: this.vehicleId }).then((res) => {
        if (res.state) {
          this.onlineInfo = res.data;
        }
      });
    },
    // 获取分组列表
    getGroupList() {
      this.groupList = {};
      getGroupList({ areaId: this.amrInfo.amrPo.areaId }).then((res) => {
        if (res.state && res.data) {
          res.data.forEach((item) => {
            this.$set(this.groupList, item.id, item.groupName);
          });
        }
      });
    },
    // 获取充电桩列表
    getChargePileList() {
      this.chargePileList = {};
      if (this.amrInfo.modelIds.length > 0) {
        let params = {
          chargeModelIds: this.amrInfo.modelIds,
          areaId: this.amrInfo.amrPo.areaId,
        };
        getChargePileList(params).then((res) => {
          if (res.state && res.data.data) {
            res.data.data.forEach((item) => {
              if (
                this.mapList[item.mapId] &&
                this.domainList[this.mapList[item.mapId].areaId]
              ) {
                this.$set(this.chargePileList, item.id, item);
              }
            });
          }
        });
      }
    },
    // 获取休息站列表
    getRestList() {
      this.restList = {};
      getRestStationList({ areaId: this.amrInfo.amrPo.areaId }).then((res) => {
        if (res.state) {
          let list = res.data.list || res.data || [];
          list.forEach((item) => {
            this.$set(this.restList, item.id, item);
          });
        }
      });
      this.getVehicleRest();
    },
    // 修改设备名称
    updateVehicleName(value) {
      if (!value) return Message.warning("设备名称不得为空");
      let params = {
        id: this.vehicleId,
        name: value,
      };
      updateVehicle(params).then((res) => {
        if (res.state) {
          Message.success("设备名称修改成功");
          this.isEditName = false;
          this.getVehicleDetail();
        }
      });
    },
    // 修改设备区域
    updateVehicleArea(value) {
      let params = {
        id: this.vehicleId,
        areaId: value,
      };
      updateVehicleArea(params).then((res) => {
        if (res.state) {
          Message.success("设备区域修改成功");
          this.isEditArea = false;
          this.getVehicleDetail();
        }
      });
    },
    // 修改设备分组
    updateVehicleGroup(value) {
      let params = {
        id: this.vehicleId,
        groupId: value,
      };
      updateVehicleGroup(params).then((res) => {
        if (res.state) {
          Message.success("设备分组修改成功");
          this.isEditGroup = false;
          this.getVehicleDetail();
        }
      });
    },
    // 修改设备启用状态
    updateVehicleEnable(value) {
      let msg = value === 1 ? "启用" : "禁用";
      let params = {
        id: this.vehicleId,
        enable: value,
      };
      toggleVehicleEnable(params).then((res) => {
        if (res.state) {
          Message.success(`设备${msg}成功`);
          this.isEditEnable = false;
          this.getVehicleDetail();
        }
      });
    },
    // 修改设备充电桩类型
    updateVehicleModel(value) {
      let params = {
        amrId: this.vehicleId,
        adaptation: "",
      };
      if (value.length > 0) params.adaptation = value.join(",");
      updateVehicleModel(params).then((res) => {
        if (res.state) {
          Message.success(`设备充电桩类型修改成功`);
          this.isEditModel = false;
          this.getVehicleDetail();
        }
      });
    },
    // 修改设备充电桩
    updateVehicleCharge(value) {
      let params = {
        amrId: this.vehicleId,
        isForceBind: this.amrInfo.forceBind,
      };
      if (value) params.chargePileId = value;
      updateVehicleCharge(params).then((res) => {
        if (res.state) {
          Message.success(`设备充电桩修改成功`);
          this.isEditCharge = false;
          this.getVehicleDetail();
        }
      });
    },
    // 修改设备绑定休息站；如果为空，则删除
    updateVehicleRest(value) {
      if (this.restInfo.id) {
        let params = {
          amrId: this.vehicleId,
          restStationId: value,
          idle: this.restInfo.idle,
          id: this.restInfo.id,
        };
        updateVehicleRest(params).then((res) => {
          if (res.state) {
            Message.success(`设备休息站配置修改成功`);
            this.isEditRest = false;
            this.getVehicleDetail();
            this.getVehicleRest();
          }
        });
      } else {
        let params = { amrId: this.vehicleId, idle: 60 };
        if (value) params.restStationId = value;
        addVehicleRest(params).then((res) => {
          if (res.state) {
            Message.success(`设备休息站配置成功`);
            this.isEditRest = false;
            this.getVehicleDetail();
            this.getVehicleRest();
          }
        });
      }
    },
    // 修改设备空闲回休息站时间；如果为空，则删除
    updateRestIdle(value) {
      if (this.restInfo.id) {
        if (value || value === 0) {
          let params = {
            amrId: this.vehicleId,
            restStationId: this.restInfo.restStationId,
            idle: value,
            id: this.restInfo.id,
          };
          updateVehicleRest(params).then((res) => {
            if (res.state) {
              Message.success(`空闲回休息站时间修改成功`);
              this.isEditRestIdle = false;
              this.getVehicleDetail();
              this.getVehicleRest();
            }
          });
        } else {
          deleteVehicleRest({ amrId: this.vehicleId }).then((res) => {
            if (res.state) {
              Message.success(`设备已删除绑定休息站`);
              this.isEditRestIdle = false;
              this.getVehicleDetail();
              this.getVehicleRest();
            }
          });
        }
      } else {
        if (!value && value !== 0) return;
        let params = { amrId: this.vehicleId, idle: value };
        addVehicleRest(params).then((res) => {
          if (res.state) {
            Message.success(`设备休息站配置成功`);
            this.isEditRestIdle = false;
            this.getVehicleDetail();
            this.getVehicleRest();
          }
        });
      }
    },
    // 关闭所有编辑
    closeAll() {
      this.isEditArea = false;
      this.isEditName = false;
      this.isEditGroup = false;
      this.isEditModel = false;
      this.isEditCharge = false;
      this.isEditEnable = false;
      this.isEditRest = false;
      this.isEditRestIdle = false;
    },
    // 返回来源页面
    goBack() {
      this.$router.push(this.backPage);
    },
    // 跳转至设备列表添加设备
    toAddDevice() {
      this.$router.push({
        path: "/device/amr",
        query: { type: "add", backPage: this.$route.fullPath },
      });
    },
    // 跳转设备列表查询设备
    toDeviceList() {
      this.$router.push({
        path: "/device/amr",
        query: { backPage: this.$route.fullPath },
      });
    },
    setChargeLevel() {
      this.$router.push({
        path: "/config/chargeLevel",
        query: { amrId: this.vehicleId, backPage: this.$route.fullPath },
      });
    },
    // 设置默认图片
    setDefaultImg() {
      this.imgUrl = "./static/img/device/no-img.png";
    },
    // 获取占用点位列表
    getLockedPoints() {
      getLockedPoints({ amrId: this.vehicleId }).then((res) => {
        if (res.state) {
          this.lockedPoints = res.data.filter((item) => !isFAKEPoint(item));
        }
      });
    },
    // 释放占用点位
    releaseLockedPoints() {
      MessageBox.confirm(`确定释放小车占用点位?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          releaseLockedPoints({ amrId: this.vehicleId }).then((res) => {
            if (res.state) {
              Message.success("小车占用点位释放成功");
              this.getLockedPoints();
            }
          });
        })
        .catch(() => {});
    },
    // 获取小车充电桩强制绑定情况
    getChargeForceBind() {
      getChargeForceBind({ amrId: this.vehicleId }).then((res) => {
        if (res.state) {
          this.$set(this.amrInfo, "forceBind", res.data);
        }
      });
    },
  },
  beforeDestroy() {
    this.onlineTimer && clearInterval(this.onlineTimer);
  },
};
</script>

