import request from '@/utils/request'

// 获取休息站列表
export function getRestStationList(data) {
  return request.post('rest/station/list', data)
}

// 新增休息站
export function addRestStation(data) {
  return request.post('rest/station/add', data)
}

// 修改休息站
export function updateRestStation(data) {
  return request.post('rest/station/update', data)
}

// 删除休息站
export function deleteRestStation(id) {
  return request.delete('rest/station/delete/' + id)
}